export class Blogs {
  public _id: string;
  public title: string;
  public name: string;
  public topic: string;
  public imageLink: string;
  public summary: string;
  public blog: string;

  constructor(title: string, name: string, topic: string, imageLink: string, summary: string, blog: string) {
    //built in function every class has which will execute to create a new instance of this class
    this.title = title;
    this.name = name;
    this.topic = topic;
    this.imageLink = imageLink;
    this.summary = summary;
    this.blog = blog;
  }
}
